
import video from 'react-video-js-player';
import React from 'react';
import royce from './assets/rolls-royce.mp4';
import royce_image from './assets/rolls.png';
import apple from './assets/apple.jpg';

class PlayVideo extends React.Component{

    // constructor(props){
    //     super(props)

    //     this.state ={
    //         urls: [
    //             {url: royce, type:  },
    //             // {url: "https://www.youtube.com/watch?v=when8G1nEoA", type: "video/mp4" },
    //             {url: "http://qthttp.apple.com.edgesuite.net/1010qwoeiuryfg/sl.m3u8", type: "application/x-mpegURL" }
    //         ]
    //     };  
        
    // }

    render(){
        return(
            <div>
                <video
                    id="my-video"
                    className="video-js vjs-theme-city vjs-big-play-centered" 
                    controls
                    style={{width:"1100px",height:"600px"}}
                    poster={apple}
                    data-setup="{}"
                >   
                    <source src="https://s3-us-west-2.amazonaws.com/hls-playground/hls.m3u8" type="application/x-mpegURL" 
                    headers='{"Access-Control-Allow-Origin":"*","Access-Control-Allow-Methods": "GET","Access-Control-Allow-Headers": "Content-Type"}' />

                    <p className="vjs-no-js">
                        To view this video please enable JavaScript, and consider upgrading to a
                        web browser that
                        <a href="https://videojs.com/html5-video-support/">supports HTML5 video</a>
                    </p>
                </video>
                <br/>
                <label style={{color:"white",fontSize:"22px",marginLeft:"-150px"}}>Http live streaming video using videojs player</label>
                <br/>
                <br/>
                <video
                    id="my-video"
                    className="video-js vjs-theme-city vjs-big-play-centered" 
                    controls
                    preload="auto"
                    style={{width:"1100px",height:"600px"}}
                    poster={royce_image}
                    data-setup="{}"
                >   
                    <source src={royce} type="video/mp4"/>

                    <p className="vjs-no-js">
                        To view this video please enable JavaScript, and consider upgrading to a
                        web browser that
                        <a href="https://videojs.com/html5-video-support/" >supports HTML5 video</a>
                    </p>
                </video>
                <br/>
                <label style={{color:"white",fontSize:"22px",marginLeft:"-150px"}} >Normal video using videojs player</label>
                <br/>
                <br/>
            </div>

        );
    }

}


export default PlayVideo;

